*:after,
*:before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

.articles:after {
  content: "";
  display: table;
  clear: both; }

*, *:after, *:before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

body {
  font: 100% Helvetica, sans-serif;
  color: #333; }

.header {
  display: -ms-flexbox;
  display: flex;
  height: 100px;
  background: #ccc;
  margin-bottom: 10px; }
  .header .header__name {
    margin: 20px auto 0 auto;
    font-size: 20px;
    background: #fff;
