.articles {
  /*display: grid;
  grid-template-columns: repeat($n,1fr);
  grid-auto-rows: max-content;
  grid-gap: 10px;*/
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
  grid-auto-rows: 100%;
  grid-auto-flow: dense; }
  @media (min-width: 480px) {
    .articles {
      /*display: grid;
      grid-template-columns: repeat($n,1fr);
      grid-auto-rows: max-content;
      grid-gap: 10px;*/
      display: grid;
      grid-gap: 10px;
      grid-template-columns: repeat(auto-fill, minmax(33.3333333%, 1fr));
      grid-auto-rows: 33.3333333%;
      grid-auto-flow: dense; } }
  @media (min-width: 768px) {
    .articles {
      /*display: grid;
      grid-template-columns: repeat($n,1fr);
      grid-auto-rows: max-content;
      grid-gap: 10px;*/
      display: grid;
      grid-gap: 10px;
      grid-template-columns: repeat(auto-fill, minmax(25%, 1fr));
      grid-auto-rows: 25%;
      grid-auto-flow: dense; } }
  @media (min-width: 1024px) {
    .articles {
      /*display: grid;
      grid-template-columns: repeat($n,1fr);
      grid-auto-rows: max-content;
      grid-gap: 10px;*/
      display: grid;
      grid-gap: 10px;
      grid-template-columns: repeat(auto-fill, minmax(15%, 1fr));
      grid-auto-rows: 15%;
      grid-auto-flow: dense; } }
  .articles .article {
    position: relative;
    height: 200px; }
    .articles .article .article__score {
      position: absolute;
      font-size: 12px;
      top: 10px;
      left: 10px;
      text-align: center;
      z-index: 1;
      color: indianred; }
      .articles .article .article__score img {
