  .articles .active {
    height: 410px;
    grid-column-end: span 1;
    grid-row-end: span 1;
    grid-row-start: span 1; }
    @media (min-width: 480px) {
      .articles .active {
        grid-column-end: span 2;
        grid-row-end: span 2;
        grid-row-start: span 2; } }
    @media (min-width: 768px) {
      .articles .active {
        grid-column-end: span 2;
        grid-row-end: span 2;
        grid-row-start: span 2; } }
    @media (min-width: 1024px) {
      .articles .active {
        grid-column-end: span 2;
        grid-row-end: span 2;
        grid-row-start: span 2; } }
    .articles .active .article__wrapper {
      background-color: #e6cea1;
      overflow: auto; }

.articles-more {
  background: #e6e6e6;
  padding: 10px;
  margin-top: 10px;
  text-align: center; }
  .articles-more .articles-more__button {
    padding: 10px 40px;
